import { config } from "./firebase-config.js";

// Detect if running in Node.js environment
const isNode = typeof window === 'undefined' && typeof global !== 'undefined';

let app;

if (isNode) {
  // Server-side initialization using firebase-admin
  try {
    const { initializeApp, getApps, cert } = await import('firebase-admin/app');
    
    // Check if app is already initialized
    if (getApps().length === 0) {
      let serviceAccount;
      
      try {
        // Try to load service account key from functions directory
        const { default: serviceAccountKey } = await import('../functions/lib/serviceAccountKey.json', {
          assert: { type: 'json' }
        });
        serviceAccount = serviceAccountKey;
      } catch (error) {
        console.warn('Service account key not found, using default credentials');
        serviceAccount = undefined;
      }
      
      // Initialize admin app
      app = initializeApp({
        credential: serviceAccount ? cert(serviceAccount) : undefined,
        projectId: config.projectId,
        storageBucket: config.storageBucket
      });
    } else {
      app = getApps()[0];
    }
  } catch (error) {
    console.error('Failed to initialize Firebase Admin SDK:', error);
    throw error;
  }
} else {
  // Client-side initialization using firebase/app
  const { initializeApp } = await import('firebase/app');
  app = initializeApp(config);
}

export { app };
